/* eslint-disable no-console */ //  eslint  console.log

import React, { Component, createContext } from "react"; //  React  createContext   React
import Service from "../Service/Service"; //   Service   Service.js
import SearchTab from "../SearchTab/SearchTab"; //   SearchTab   SearchTab.js
import RatedTab from "../RatedTab/RatedTab"; //   RatedTab   RatedTab.js
import Alert from "../Alert/Alert"; //   Alert   Alert.js
import "../default-style/default-style.css"; //    default-style.css
import "./MovieApp.css"; //    default-style.css

export const Context = createContext([]); //       

export default class MovieApp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentTab: "search", //     - "search"
      isOnline: navigator.onLine, //      
      genresList: [], //  
    };
    this.switchTab = this.switchTab.bind(this); //     switchTab
    this.handleOnline = this.handleOnline.bind(this); //     handleOnline
    this.handleOffline = this.handleOffline.bind(this); //     handleOffline

    this.service = new Service(); //    Service
  }

  async componentDidMount() {
    window.addEventListener("online", this.handleOnline); //    online
    window.addEventListener("offline", this.handleOffline); //    offline

    const genres = await this.service.getGenres(); //      
    this.setState({ genresList: genres }); //     
  }

  componentWillUnmount() {
    window.removeEventListener("online", this.handleOnline); //    online
    window.removeEventListener("offline", this.handleOffline); //    offline
  }

  //     online
  handleOnline() {
    this.setState({ isOnline: true }); //   isOnline  true
  }

  //     offline
  handleOffline() {
    this.setState({ isOnline: false }); //   isOnline  false
  }

  //    
  switchTab(tab) {
    this.setState({ currentTab: tab }); //   
  }

  render() {
    const { currentTab, isOnline, genresList } = this.state; //  

    let content;
    if (!isOnline) {
      //   -
      content = (
        <Alert
          className="alert"
          type="error"
          description=""
          message="   ."
        />
      );
    } else {
      //     
      content = currentTab === "search" ? <SearchTab /> : <RatedTab />; //        
    }

    console.log(genresList); //     

    return (
      <Context.Provider value={genresList}>
        {/*       */}
        <div className="container">
          {" "}
          {/*    */}
          <div className="wrapper-tabs">
            {/*    */}
            <button
              type="button"
              className={currentTab === "search" ? "tab active" : "tab"} //      "Search"
              onClick={() => this.switchTab("search")} //       "Search"
            >
              Search
            </button>
            <button
              type="button"
              className={currentTab === "rated" ? "tab active" : "tab"} //      "Rated"
              onClick={() => this.switchTab("rated")} //       "Rated"
            >
              Rated
            </button>
          </div>
          {content} {/*       */}
        </div>
      </Context.Provider>
    );
  }
}
