/* eslint-disable react/prop-types */ //   prop-types   
import React, { Component } from "react";
import Service from "../Service/Service"; //      
import MovieSearch from "../MovieSearch/MovieSearch"; //    
import MovieList from "../MovieList/MovieList"; //    
import Spin from "../Spin/Spin"; //   
import Alert from "../Alert/Alert"; //     

export default class SearchTab extends Component {
  constructor(props) {
    super(props);
    //   
    this.state = {
      movies: [], //  
      loading: false, //  
      error: null, // 
      query: "", //  
      searchPerformed: false, //   
      currentPage: 1, //  
      totalPages: 0, //   
    };
    //     
    this.handleSearchQueryChange = this.handleSearchQueryChange.bind(this);
    this.handlePageChange = this.handlePageChange.bind(this);
    this.setMovies = this.setMovies.bind(this);
    this.setError = this.setError.bind(this);
    this.Service = new Service({
      //      
      setMovies: this.setMovies,
      setError: this.setError,
    });
    this.handleRatingChange = this.handleRatingChange.bind(this);
  }

  //    
  handleSearchQueryChange(query) {
    this.setState({ query, loading: true, searchPerformed: true });
  }

  //    
  handlePageChange(page) {
    this.setState({ currentPage: page, loading: true }, () => {
      const { query, currentPage } = this.state;
      //         
      this.Service.fetchMovies(query, currentPage);
    });
  }

  //    
  handleRatingChange(movieId, rating) {
    const { movies } = this.state;
    const updatedMovies = movies.map((movie) => {
      if (movie.id === movieId) {
        return { ...movie, rating };
      }
      return movie;
    });

    //      
    this.setState({ movies: updatedMovies }, () => {
      localStorage.setItem("movies", JSON.stringify(updatedMovies));

      //      localStorage
      const existingRatedMovies =
        JSON.parse(localStorage.getItem("ratedMovies")) || [];

      //     
      const updatedRatedMovies = existingRatedMovies.filter(
        (movie) => movie.id !== movieId,
      );
      if (rating > 0) {
        const ratedMovie = updatedMovies.find((movie) => movie.id === movieId);
        updatedRatedMovies.push(ratedMovie);
      }

      localStorage.setItem("ratedMovies", JSON.stringify(updatedRatedMovies));
    });
  }

  //     
  setMovies(movies, totalPages) {
    this.setState({ movies, totalPages, loading: false, error: null });
  }

  //    
  setError(error) {
    this.setState({ error, loading: false });
  }

  render() {
    const {
      movies,
      loading,
      error,
      searchPerformed,
      currentPage,
      totalPages,
      query,
    } = this.state;

    return (
      <div>
        {/*    */}
        <MovieSearch onQueryChange={this.handleSearchQueryChange} />
        {/*  ,   */}
        {error && (
          <Alert
            type="error"
            message="Oops, something went wrong!"
            description={error.message}
          />
        )}
        {/*      */}
        {loading && <Spin />}
        {/*      */}
        <Service
          ref={(movieService) => {
            this.Service = movieService;
          }}
          setMovies={this.setMovies}
          setError={this.setError}
          searchWord={query}
        />
        {/* ,   ,     */}
        {searchPerformed && movies.length === 0 && !error && !loading && (
          <Alert description="" message="  " type="info" />
        )}
        {/*    */}
        {!error && movies.length > 0 && (
          <MovieList
            movies={movies}
            loading={loading}
            searchPerformed={searchPerformed}
            currentPage={currentPage}
            totalPages={totalPages}
            onPageChange={this.handlePageChange}
            onRatingChange={(movieId, rating) => {
              this.handleRatingChange(movieId, rating);
            }}
          />
        )}
      </div>
    );
  }
}
